html {
  font-family: Roboto !important;
}
html {
  font-size: 62.5%;
}
body {
  margin: 0;
  height: 100vh;
}
.horizontal-layout {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.horizontal-layout.must-expand-max-state {
  height: 100%;
}
.horizontal-layout__item {
  flex: 1;
}
.horizontal-layout__item.must-expand-max-state {
  height: 100vh;
}
.horizontal-layout__item.is-three-tenths-state {
  flex: 0.3;
}
.horizontal-layout__item.is-seven-tenths-state {
  flex: 0.7;
}
.horizontal-layout__item.must-expand-double-state {
  flex: 2;
}
.horizontal-layout__item.must-fit-content-state {
  flex-grow: 0;
}
.vertical-layout {
  display: flex;
  flex-direction: column;
}
.vertical-layout__item {
  flex: 1;
}
.vertical-layout__item.must-expand-max-state {
  height: 100vw;
}
.vertical-layout__item.must-fit-content-state {
  flex-grow: 0;
}
.vertical-layout__item.must-expand-double-state {
  flex: 2;
}
.vertical-layout__item.is-three-tenths-state {
  flex: 0.3;
}
.wait-panel-layout {
  position: relative;
  width: 100%;
  height: 100%;
}
.wait-panel-layout__content {
  width: 100%;
  height: 100%;
}
.wait-panel-layout__glass {
  z-index: 100;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
}
.wait-panel-layout__glass.is-overlay-all-page-state {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  align-items: center;
}
.wait-panel-layout__front {
  display: flex;
  flex-direction: column;
  position: relative;
}
.wait-panel-layout__area {
  position: sticky;
  top: 0;
}
.wait-panel-layout__spinner {
  display: flex;
  align-items: center;
  justify-content: center;
}
.wait-panel-layout__description {
  display: block;
  max-width: 300px;
  margin-top: 15px;
  align-self: center;
}
.wait-panel-layout__logo {
  position: absolute;
  align-self: center;
  justify-self: center;
  width: 50px;
  height: 50px;
}
.wait-panel-layout__separator {
  min-height: 250px;
  max-height: 500px;
}
.wait-panel-layout__separator.is-overlay-all-page-state {
  min-height: unset;
  max-height: unset;
}